<template>
    <div class="row">
        <div class="col-xxl-8 offset-xxl-8 col-xl-10 offset-xl-7 col-lg-10 offset-lg-7 col-md-12 offset-md-6 col-sm-18 offset-sm-3">
            <div class="login-page">
                <div class="header acenter">
                      
                </div>
                <form action="" @submit.prevent="logIn">
