    import { mapState, mapMutations, mapGetters } from 'vuex'
    export default {
        name: 'App',
        data() {
            return {
                email: '',
                password: ''
            }
        },
        computed: {
            ...mapState([
                'auth'
            ])
        },
        methods: {
            logIn() {
                //   
